'use client';

import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';

import { useEffect, useState } from 'react';
import { API_URL } from '../api/apiURL';

export default function UserTrends() {
  const [userTrendData, setUserTrendData] = useState([]);
  const [funnelData, setFunnelData] = useState([]);

  // Format numbers like 919.98 K
  const formatK = (num) => (num / 1000).toFixed(2) + ' K';

  // Fetch Trend
  useEffect(() => {
    const fetchTrend = async () => {
      try {
        const res = await fetch(`${API_URL}/admin/user-signup-trends`);
        const result = await res.json();

        if (result.success) {
          const processed = result.data.map((item) => ({
            ...item,
            returningUsers: Math.floor(item.newUsers * 0.5),
          }));
          setUserTrendData(processed);
        }
      } catch (err) {
        console.log(err);
      }
    };
    fetchTrend();
  }, []);

  // Fetch Funnel
  useEffect(() => {
    const fetchFunnel = async () => {
      try {
        const res = await fetch(`${API_URL}/admin/search-to-match`);
        const result = await res.json();

        if (result.success) setFunnelData(result.data);
      } catch (err) {
        console.log(err);
      }
    };
    fetchFunnel();
  }, []);

  return (
    <div className="mt-24 px-10 flex flex-col md:flex-row gap-10 w-full justify-center items-start">

      {/* LEFT SECTION — SEARCH TO MATCH */}
      <div className="bg-white border border-gray-300 rounded-[22px] shadow-[0_5px_15px_rgba(0,0,0,0.12)] p-10 w-full md:w-1/2">

        <h2 className="text-[26px] font-bold mb-10">Search To Match</h2>

        <div className="space-y-10">

          {funnelData.map((item, index) => {
            const max = funnelData[0]?.value || 1;
            const percent = (item.value / max) * 100;

            return (
              <div key={index} className="flex items-center">

                {/* LABEL EXACT LEFT */}
                <div className="w-[190px] text-[18px] text-gray-700 font-semibold">
                  {item.stage}
                </div>

                {/* BLUE BAR (EXACT START + RADIUS + SIZE) */}
                <div className="flex-1 mx-4">
                  <div className="h-[24px] bg-gray-200 rounded-full overflow-hidden">
                    <div
                      className="h-full rounded-[14px]"
                      style={{
                        width: `${percent}%`,
                        background: 'linear-gradient(90deg,#3C4BFF,#001BFF)',
                        boxShadow: '0 4px 12px rgba(0,0,0,0.30)',
                      }}
                    />
                  </div>
                </div>

                {/* VALUE EXACT RIGHT */}
                <div className="w-[90px] text-right text-[18px] font-semibold text-gray-800">
                  {formatK(item.value)}
                </div>

              </div>
            );
          })}

        </div>
      </div>

      {/* RIGHT SECTION — RUNNING USER TREND */}
      <div className="bg-white border border-gray-300 rounded-[22px] shadow-[0_5px_15px_rgba(0,0,0,0.12)] p-10 w-full md:w-1/2">

        {/* TITLE + LEGEND EXACT SAME ROW */}
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-[28px] font-bold">Running User Trend</h2>

          <div className="flex gap-8 text-gray-700 text-[15px] font-medium">
            <div className="flex items-center gap-2">
              <span className="h-3 w-3 rounded-full bg-yellow-200 border-2 border-yellow-500"></span>
              <span>New Registered Users</span>
            </div>

            <div className="flex items-center gap-2">
              <span className="h-3 w-3 rounded-full bg-green-200 border-2 border-green-600"></span>
              <span>Returning Users</span>
            </div>
          </div>
        </div>

        {/* LINE CHART */}
        <div className="w-full h-[350px]">
          <ResponsiveContainer width="100%" height="100%">
            <LineChart
              data={userTrendData}
              margin={{ top: 20, right: 25, left: 60, bottom: 10 }}
            >
              <CartesianGrid stroke="#D6D6D6" strokeWidth={2} vertical={false} />

              {/* X Axis */}
              <XAxis
                dataKey="date"
                tick={{ fontSize: 18, fontWeight: 600 }}
                tickLine={false}
                axisLine={false}
                interval={0}
              />

              {/* Y Axis (EXACT LOOK) */}
              <YAxis
                ticks={[
                  0,
                  5000000,
                  10000000,
                  15000000,
                  20000000,
                  25000000,
                  30000000,
                ]}
                tickFormatter={(v) => `${v / 1000000} M`}
                tick={{ fontSize: 18, fontWeight: 600 }}
                tickLine={false}
                axisLine={false}
                tickMargin={12}
              />

              <Tooltip />

              {/* YELLOW LINE (EXACT DOT STYLE) */}
              <Line
                type="monotone"
                dataKey="newUsers"
                stroke="#facc15"
                strokeWidth={4}
                dot={{
                  r: 9,
                  strokeWidth: 4,
                  fill: '#fff',
                  stroke: '#facc15',
                }}
                activeDot={{ r: 11 }}
              />

              {/* GREEN LINE */}
              <Line
                type="monotone"
                dataKey="returningUsers"
                stroke="#16a34a"
                strokeWidth={4}
                dot={{
                  r: 9,
                  strokeWidth: 4,
                  fill: '#fff',
                  stroke: '#16a34a',
                }}
                activeDot={{ r: 11 }}
              />

            </LineChart>
          </ResponsiveContainer>
        </div>

      </div>

    </div>
  );
}
