'use client';

import {
  PieChart,
  Pie,
  Cell,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  Line,
  CartesianGrid,
} from "recharts";
import { useEffect, useState } from "react";
import { API_URL } from "../api/apiURL";

const profileColors = ["#0f8618", "#f39a1e", "#e02020", "#f6d21a"];

export default function ProfileMatch() {
  const [profileData, setProfileData] = useState([
    { name: "Completed", value: 45 },
    { name: "Low", value: 20 },
    { name: "Incomplete", value: 15 },
    { name: "Moderate", value: 20 },
  ]);

  const months = [
    "Jan","Feb","Mar","Apr","May","Jun","Jly","Aug","Sep","Oct","Nov","Dec"
  ];

  const [matchData, setMatchData] = useState([
    { month: "Jan", totalUsers: 20, matches: 10 },
    { month: "Feb", totalUsers: 24, matches: 13 },
    { month: "Mar", totalUsers: 28, matches: 19 },
    { month: "Apr", totalUsers: 23, matches: 17 },
    { month: "May", totalUsers: 19, matches: 13 },
    { month: "Jun", totalUsers: 14, matches: 8 },
    { month: "Jly", totalUsers: 12, matches: 7 },
    { month: "Aug", totalUsers: 16, matches: 9 },
    { month: "Sep", totalUsers: 18, matches: 10 },
    { month: "Oct", totalUsers: 22, matches: 12 },
    { month: "Nov", totalUsers: 27, matches: 16 },
    { month: "Dec", totalUsers: 33, matches: 27 },
  ]);

  const total = profileData.reduce((a, b) => a + b.value, 0);

  const renderLabel = ({
    cx,
    cy,
    midAngle,
    innerRadius,
    outerRadius,
    value,
  }) => {
    const RAD = Math.PI / 180;
    const radius = innerRadius + (outerRadius - innerRadius) * 0.55;
    const x = cx + radius * Math.cos(-midAngle * RAD);
    const y = cy + radius * Math.sin(-midAngle * RAD);
    const percent = Math.round((value / total) * 100);

    return (
      <text
        x={x}
        y={y}
        fill="#111"
        fontSize="14"
        fontWeight="600"
        textAnchor="middle"
        dominantBaseline="central"
      >
        {percent}%
      </text>
    );
  };

  return (
    <div className="p-6 flex flex-col lg:flex-row gap-8">

      {/* PIE BOX */}
      <div className="bg-white rounded-2xl shadow-xl p-6 border w-full lg:w-[45%]">
        <h2 className="text-xl font-semibold mb-4">Profile Overview</h2>

        {/* LEGENDS */}
        <div className="flex flex-col gap-2 text-sm font-medium mb-4">
          {profileData.map((item, i) => (
            <div className="flex items-center gap-2" key={i}>
              <div
                className="w-4 h-4 rounded border"
                style={{ backgroundColor: profileColors[i] }}
              />
              <span>{item.name}</span>
            </div>
          ))}
        </div>

        {/* PIE */}
        <div className="w-full h-[300px]">
          <ResponsiveContainer width="100%" height="100%">
            <PieChart>
              <defs>
                <filter id="dropshadow" height="130%">
                  <feGaussianBlur in="SourceAlpha" stdDeviation="4" />
                  <feOffset dx="0" dy="3" result="offsetblur" />
                  <feMerge>
                    <feMergeNode />
                    <feMergeNode in="SourceGraphic" />
                  </feMerge>
                </filter>
              </defs>

              <Pie
                data={profileData}
                cx="50%"
                cy="50%"
                innerRadius={60}
                outerRadius={100}
                paddingAngle={3}
                dataKey="value"
                stroke="#fff"
                strokeWidth={2}
                label={renderLabel}
                labelLine={false}
                style={{ filter: "url(#dropshadow)" }}
              >
                {profileData.map((_, i) => (
                  <Cell key={i} fill={profileColors[i]} />
                ))}
              </Pie>
            </PieChart>
          </ResponsiveContainer>
        </div>
      </div>

      {/* MATCHES BOX */}
      <div className="bg-white rounded-2xl shadow-xl p-6 border w-full lg:w-[55%]">
        <h2 className="text-xl font-semibold mb-4">Matches Per Month</h2>

        <ResponsiveContainer width="100%" height={350}>
          <BarChart data={matchData}>
            <CartesianGrid strokeDasharray="3 3" opacity={0.3} />
            <XAxis dataKey="month" />
            <YAxis
              domain={[0, 35]}
              ticks={[0, 5, 10, 15, 20, 25, 30, 35]}
              tick={{ fontSize: 12 }}
              unit=" M"
            />

            <Tooltip />

            <defs>
              <linearGradient id="goldBar" x1="0" y1="0" x2="0" y2="1">
                <stop offset="0%" stopColor="#ffe678" />
                <stop offset="100%" stopColor="#f4c116" />
              </linearGradient>
            </defs>

            <Bar
              dataKey="totalUsers"
              fill="url(#goldBar)"
              radius={[12, 12, 0, 0]}
            />

            <Line
              type="monotone"
              dataKey="matches"
              stroke="#e02020"
              strokeWidth={3}
              dot={{
                r: 6,
                fill: "#ffe678",
                stroke: "#e02020",
                strokeWidth: 2,
              }}
            />
          </BarChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
